html,
body {
  overflow-x: hidden;
  max-width: 100%;
}

main {
  display: block;
  max-width: 100%;
  height: 100vh;
  background-color: #7258f8;
}
.title {
  line-height: 87px;
  font-weight: 400;
  font-size: 72px;
  letter-spacing: -0.025;
  text-align: center;
  background: linear-gradient(90deg, #ffe23d 14.46%, #00ffd2 100%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  z-index: 1;
}
.span {
  font-weight: 700;
}
.code {
  position: absolute;
  top: -300px;
  left: 750px;
  background: linear-gradient(to top, #7258f8 25%, #ffffff44 45%, #7258f8 100%);
  background-clip: text;
  -moz-background-clip: text;
  -webkit-text-fill-color: transparent;
  float: right;
  font-size: 230px;
  letter-spacing: -20px;
  font-family: sans-serif;
  font-weight: 1000;
  z-index: 0;
}
.head {
  display: flex;
  justify-content: space-evenly;
}
.row-2 {
  max-width: 100%;
  display: flex;
  align-items: center;
}
.input {
  margin-left: auto;
  width: 733px;
  height: 69px;
  text-align: center;
  line-height: 60px;
  border: none;
  font-weight: bold;
  font-size: larger;
  color: white;
  background: #4c38af;
  border-radius: 35px;
}
.btn {
  margin-left: 10px;
  width: 85.56px;
  height: 37px;
  cursor: pointer;
  border: none;
  font-size: larger;
  color: white;
  background-color: #2cbea4;
  border-radius: 57.8125px;
}
.top-img {
  position: absolute;
  width: 77px;
  height: 88.15px;
  left: 33px;
  top: 24px;
}
.g33-yellow {
  margin-left: 20px;
  width: 89px;
  height: 89px;
  left: 21px;
}
.arrow {
  margin-left: auto;
  width: 100px;
  height: 120px;
}
.shape {
  margin-top: 101px;
  padding: -30px;
  width: 254.26px;
  height: 220.51px;
}
.code-board {
  padding: 16px 50px;
  max-width: 100%;
}
.last {
  margin-left: auto;
  width: 185px;
  height: 185px;
}
.frame {
  position: absolute;
  width: 106px;
  height: 106px;
  left: 870px;
  top: 335px;
}
.frame-2 {
  position: absolute;
  width: 25px;
  height: 25px;
  left: 406px;
  top: 383px;
}
.final {
  display: flex;
  align-items: center;
}
.container {
  display: flex;
  margin-left: 50px;
  align-items: center;
}
